
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import {editLoginPwd} from '../config'
export default {
  name: 'passwordLogin',
  head: {
    title: {
      inner: '修改登录密码'
    }
  },
  data () {
    return {
      loading: false,
      pwd: '',
      form: {
        userId: '',
        oldPwd: '',
        newPwd: ''
      }
    }
  },
  created () {
    this.form.userId = JSON.parse(this.$localStorage.get('userInfo')).userId
  },
  components: {
    Group,
    XInput,
    XButton
  },
  methods: {
    handleSubmit () {
      const reg = /^[0-9a-zA-Z]+$/
      if (!this.form.oldPwd) {
        this.$vux.toast.show({
          type: 'text',
          width: '15em',
          position: 'bottom',
          text: '请输入旧密码！',
          time: '1000'
        })
      } else if (!this.form.newPwd || !this.pwd) {
        this.$vux.toast.show({
          type: 'text',
          width: '15em',
          position: 'bottom',
          text: '请输入新密码！',
          time: '1000'
        })
      } else if (this.form.newPwd !== this.pwd) {
        this.$vux.toast.show({
          type: 'text',
          width: '15em',
          position: 'bottom',
          text: '两次输入的密码不同！',
          time: '1000'
        })
      } else if (!reg.test(this.form.newPwd)) {
        this.$vux.toast.show({
          type: 'text',
          width: '20em',
          position: 'bottom',
          text: '请输入6-20位字母和数字的组合！',
          time: '1000'
        })
      } else {
        this.$http({
          method: 'jsonp',
          url: editLoginPwd,
          jsonp: 'callback',
          jsonpCallback: 'json',
          params: this.form,
          before: () => {
            this.loading = true
          }
        })
        .then(res => {
          this.loading = false
          if (res.body.status) {
            this.$vux.toast.show({
              type: 'text',
              width: '20em',
              position: 'bottom',
              text: '密码修改成功！',
              time: '1000'
            })
            setTimeout(() => {
              this.$router.replace('/login')
            }, 1000)
            this.$localStorage.set('logined', false)
            this.$localStorage.remove('userInfo')
          } else {
            this.$vux.toast.show({
              type: 'text',
              width: '15em',
              position: 'bottom',
              text: res.body.msg,
              time: '1000'
            })
          }
        })
      }
    }
  }
}



